<script>
import { onBeforeUpdate, reactive, toRefs, ref } from '@vue/composition-api'

export default {
  setup(_, { root }) { 
    const tab = reactive({
      tabs: [
        {
          label: 'Case Info',
          class: 'fas fa-chart-pie'
        }, 
        {
